import type { Contents } from '@jupyterlab/services/lib/contents';
import {
  createSkeleton,
  getAllNotebooks,
  saveAndCloseAll,
  setDocument
} from './util';
import { IDocumentManager } from '@jupyterlab/docmanager';
import { ILabShell } from '@jupyterlab/application';
import { INotebookContent } from '@jupyterlab/nbformat/lib/index';

interface IApiMessage {
  destination: 'host' | 'child';
}

export interface IApiRequest extends IApiMessage {
  type: 'rs-req';
  fn: string;
  args: Array<any>;
  nonce: number;
}

export interface IApiResponse extends IApiMessage {
  type: 'rs-res';
  error: boolean;
  data: any;
  nonce: number;
}

export interface IApiPing extends IApiMessage {
  type: 'rs-ping';
}

export interface IApiPong extends IApiMessage {
  type: 'rs-pong';
}

export class ApiMessenger {
  private readonly resolutions_: Map<
    number,
    { resolve: (ret: any) => void; reject: (err: any) => void }
  >;

  private readonly waiting_: Array<IApiRequest>;

  private ready_ = false;
  private shuttingDown_ = false;
  private onFinish_: (() => void) | null = null;
  private nonce_ = 0;

  private iam_: 'child' | 'host';
  private destination_: 'child' | 'host';
  private responseHandler_: ((fn: string, ...args: any[]) => any) | null;
  private window_: Window;

  constructor(
    iam: ApiMessenger['iam_'],
    responseHandler?: ApiMessenger['responseHandler_']
  ) {
    this.iam_ = iam;
    this.responseHandler_ = responseHandler ?? null;
    this.destination_ = iam === 'child' ? 'host' : 'child';
    this.window_ = iam === 'child' ? window.parent : window.frames[0];
    this.handler_ = this.handler_.bind(this);
    this.waiting_ = [];
    this.resolutions_ = new Map();
    window.addEventListener('message', this.handler_);
    this.doSend_({ type: 'rs-ping', destination: this.destination_ });
  }

  get ready() {
    return this.ready_;
  }

  destroy() {
    this.shuttingDown_ = true;
    if (this.resolutions_.size == 0) {
      window.removeEventListener('message', this.handler_);
    } else {
      this.onFinish_ = () =>
        window.removeEventListener('message', this.handler_);
    }
  }

  private onReady() {
    this.ready_ = true;
    for (const req of this.waiting_) {
      this.doSend_(req);
    }
    this.waiting_.splice(0, this.waiting_.length);
  }

  private async handler_(
    e: MessageEvent<IApiPong | IApiResponse | IApiPing | IApiRequest>
  ) {
    if (e.data?.destination !== this.iam_) {
      return;
    }
    console.log(this.iam_, 'HANDLED', e);

    switch (e.data.type) {
      case 'rs-res':
        const resolution = this.resolutions_.get(e.data.nonce);
        if (!resolution) break;

        if (e.data.error) {
          resolution.reject(e.data.data);
        } else {
          resolution.resolve(e.data.data);
        }
        this.resolutions_.delete(e.data.nonce);
        break;
      case 'rs-pong':
        this.onReady();
        break;
      case 'rs-ping':
        this.doSend_({ type: 'rs-pong', destination: this.destination_ });
        break;
      case 'rs-req':
        try {
          const result = await this.responseHandler_?.(
            e.data.fn,
            ...e.data.args
          );
          this.doSend_({
            type: 'rs-res',
            data: result,
            destination: this.destination_,
            error: false,
            nonce: e.data.nonce
          });
        } catch (ex) {
          this.doSend_({
            type: 'rs-res',
            data: String(ex),
            destination: this.destination_,
            error: true,
            nonce: e.data.nonce
          });
        }
        break;
    }
    if (this.resolutions_.size === 0 && this.onFinish_ !== null) {
      this.onFinish_();
    }
  }

  private doSend_(msg: IApiPing | IApiRequest | IApiResponse | IApiPong) {
    console.log(this.iam_, 'SENDING', msg);
    this.window_?.postMessage(msg, '*');
  }

  sendPing() {
    this.doSend_({ type: 'rs-ping', destination: this.destination_ });
  }

  async send(
    msg: Omit<IApiRequest, 'nonce' | 'type' | 'destination'>
  ): Promise<any> {
    if (this.shuttingDown_) {
      throw new Error('Messenger is shutting down...');
    }
    const full: IApiRequest = {
      ...msg,
      nonce: this.nonce_++,
      type: 'rs-req',
      destination: this.destination_
    };
    const prom = new Promise<any>((resolve, reject) => {
      this.resolutions_.set(full.nonce, { resolve, reject });
    });
    if (!this.ready) {
      this.waiting_.push(full);
    } else {
      this.doSend_(full);
    }

    return prom;
  }
}

export class JupyterResponseHandler {
  constructor(
    private contents_: Contents.IManager,
    private docs_: IDocumentManager,
    private lab_: ILabShell
  ) {}

  handler = async (fn: string, ...args: any[]): Promise<any> => {
    if (!(fn in this)) {
      throw new Error(`${fn} is not a valid function.`);
    }
    return await (this as any)[fn](...args);
  };

  async getAllNotebooks() {
    return await getAllNotebooks(this.contents_);
  }

  async setupNotebook(nb: Array<string>): Promise<void>;
  async setupNotebook(nb: INotebookContent): Promise<void>;
  async setupNotebook(nb: INotebookContent | Array<string>): Promise<void> {
    await saveAndCloseAll(this.lab_, this.docs_);
    if (Array.isArray(nb)) {
      await createSkeleton(this.docs_, nb);
    } else {
      await setDocument(this.docs_, nb);
    }
  }
}
