import { ILabShell } from '@jupyterlab/application';
import { IDocumentManager } from '@jupyterlab/docmanager';
import { Notebook } from '@jupyterlab/notebook';
import type { Contents } from '@jupyterlab/services/lib/contents';
import { INotebookContent } from '@jupyterlab/nbformat/lib/index';

export interface INotebookFile {
  path: string;
  contents: string;
}

export async function getAllNotebooks(
  contents: Contents.IManager,
  folder = '',
  exploredPaths?: Set<string>
): Promise<INotebookFile[]> {
  exploredPaths = exploredPaths ?? new Set<string>();
  const data = await contents.get(folder);
  let notebooks: INotebookFile[] = [];
  for (const el of data.content as Contents.IModel[]) {
    const path = (folder + '/' + el.name).replace(/^\//, '');
    if (exploredPaths.has(path) || /node_modules/g.test(path)) {
      continue;
    }
    console.log('Explored', exploredPaths.size, 'paths');
    exploredPaths.add(path);

    if (el.type === 'directory') {
      notebooks = notebooks.concat(
        await getAllNotebooks(contents, path, exploredPaths)
      );
    } else if (el.type === 'notebook') {
      const nb = await contents.get(path, { content: true });
      notebooks.push({
        path,
        contents: nb.content
      });
    }
  }
  return notebooks;
}

export async function saveAndCloseAll(lab: ILabShell, docs: IDocumentManager) {
  const promises: Array<Promise<void>> = [];
  for (const w of lab.widgets('main')) {
    const ctx = docs.contextForWidget(w);
    if (!ctx) {
      continue;
    }
    promises.push(ctx.save().catch(e => /* ignore ðŸ˜ƒ */ void e));
  }
  await Promise.all(promises);
  lab.closeAll();
}

export async function createSkeleton(
  docs: IDocumentManager,
  starter: string[]
) {
  try {
    await docs.deleteFile('test.ipynb');
  } catch (e) {
    console.log("it probably didn't exist or something", e);
  }
  const doc = await docs.newUntitled({ type: 'notebook', ext: 'ipynb' });
  const widget = await docs.openOrReveal(doc.path, 'notebook');
  if (!widget) throw new Error('!!!');

  widget.isUntitled = true;
  await widget.context.ready;
  const nb = widget.content as Notebook;

  for (let i = 0; i < starter.length; i++) {
    nb.model?.sharedModel.insertCell(i, {
      cell_type: 'code',
      source: starter[i].trim()
    });
  }
  await widget.context.ready;
}

export async function setDocument(
  docs: IDocumentManager,
  content: INotebookContent
) {
  try {
    await docs.deleteFile('test.ipynb');
  } catch (e) {
    console.log("it probably didn't exist or something", e);
  }
  const nb = (docs.createNew('test.ipynb') as any)?.content as Notebook;
  nb.model?.sharedModel.fromJSON(content);
}
